<template>
  <div id="app">
    <el-form inline>
      <el-form-item label="词库">
        <el-select size="small" v-model="selectIdiom">
          <el-option
            key="idiom.clear"
            label="idiom.clear"
            value="idiom.clear"
          />
          <el-option
            key="idiom.clear.fill"
            label="idiom.clear.fill"
            value="idiom.clear.fill"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="最小成语数">
        <el-select size="small" v-model="minIdiom">
          <el-option key="3" label="3" :value="3" />
          <el-option key="4" label="4" :value="4" />
          <el-option key="5" label="5" :value="5" />
          <el-option key="6" label="6" :value="6" />
          <el-option key="7" label="7" :value="7" />
          <el-option key="8" label="8" :value="8" />
          <el-option key="9" label="9" :value="9" />
          <el-option key="10" label="10" :value="10" />
          <el-option key="11" label="11" :value="11" />
          <el-option key="12" label="12" :value="12" />
          <el-option key="13" label="13" :value="13" />
        </el-select>
      </el-form-item>
    </el-form>

    <div class="grid">
      <div
        class="grid-row"
        v-for="rowIndex in 8"
        :key="rowIndex"
        :data-row="rowIndex"
      >
        <div
          class="grid-col"
          v-for="colIndex in 10"
          :key="colIndex"
          :data-col="colIndex"
        >
          <v-cell
            ref="grid_items"
            class="grid-item"
            :row-index="rowIndex"
            :col-index="colIndex"
            :selections="getGridAxis"
            :current="current_selections"
            :word="getWordByAxis(rowIndex, colIndex)"
            @mousedown="__mousedown"
            @mouseup="__mouseup"
            @mousemove="__mousemove"
          >
          </v-cell>
        </div>
      </div>
    </div>

    <div class="padding">
      <div class="status">
        点击划过4个格子生成成语，右键抠字。 当前成语：<span>{{
          grid.length
        }}</span
        >， 抠字：<span>{{ blanksCount }}</span>
      </div>
      <el-form inline>
        <el-form-item>
          <el-button type="danger" size="small" @click="clearStorage"
            >清除缓存</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="small"
            @click="download"
            :disabled="grid.length < rawMinIdiom"
            >下载</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="clear">清空</el-button>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="small"
            @click="setWord(true)"
            :disabled="!isExists"
            >换词</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-select size="small" v-model="value" @change="onWordChange">
            <el-option
              v-for="(item, key) in optionalWords"
              :key="key"
              :label="item.word"
              :value="item.word"
            >
              <span style="float: left">{{ item.word }}</span>
              <span style="float: left">({{ item.count }})</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{
                item.reference
              }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
            type="danger"
            size="small"
            @click="__removeNode"
            :disabled="!isExists"
            >删除</el-button
          >
        </el-form-item>
      </el-form>
      <el-form inline>
        <el-form-item label="随机生成">
          <el-input
            type="number"
            v-model="randomLevelForm.levelCount"
            size="mini"
            style="width: 60px"
          ></el-input
          >关
        </el-form-item>
        <el-form-item label="成语：">
          <el-select
            size="mini"
            v-model="randomLevelForm.wordCount"
            style="width: 55px"
          >
            <el-option
              v-for="i in 8"
              :key="i + 2"
              :label="i + 2"
              :value="i + 2"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="抠字：">
          <el-select
            size="mini"
            v-model="randomLevelForm.blankCount"
            style="width: 55px"
          >
            <el-option
              v-for="i in 8"
              :key="i + randomLevelForm.wordCount - 1"
              :label="i + randomLevelForm.wordCount - 1"
              :value="i + randomLevelForm.wordCount - 1"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
            type="success"
            size="small"
            @click="handleStartRandomLevels"
            :disabled="randomLevelForm.working"
            :loading="randomLevelForm.working"
            >{{ randomLevelForm.buttonText }}</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="small"
            @click="handleReRandomLevel"
            :disabled="randomLevelForm.currentLevel == 0"
            >重新生成</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="small"
            @click="handleSaveLevel"
            :disabled="randomLevelForm.currentLevel == 0"
            >保存</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            size="small"
            @click="handleDownloadAll"
            :disabled="randomLevelForm.storeLevelCount == 0"
            >导出</el-button
          >
        </el-form-item>
      </el-form>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="randomLevelForm.currentPage"
        :page-size="1"
        layout="prev, pager, next, jumper"
        :total="randomLevelForm.storeLevelCount"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import VCell from "@/components/cell";

import _ from "lodash";
import FileSaver from "file-saver";
import ClearIdioms from "./data/idiom.clear.json";
import ClearFillIdioms from "./data/idiom.clear.fill.json";
import localforage from "localforage";
import pLimit from "p-limit";

import { getLocalStorage, setLocalStorage } from "@/utils/storage";
import { jsonParse, jsonStringify } from "@/utils";

const KEY_IDIOM_USED_COUNT = "__idiom_used_count__";
const KEY_IDIOM_LEVEL_NUMBER = "__idiom_level_number__";
const KEY_IDIOM_MIN_NUMBER = "__idiom_min_number__";

let _selectionsArr;

export default {
  name: "App",
  components: { VCell },
  data() {
    return {
      selectIdiom: "idiom.clear",
      isSelect: false,
      current_selections: [],
      grid: [],
      value: "",
      rawGridStorage: jsonParse(getLocalStorage(KEY_IDIOM_USED_COUNT), {}),
      levelNumber: parseInt(getLocalStorage(KEY_IDIOM_LEVEL_NUMBER) || "1"),
      rawMinIdiom: parseInt(getLocalStorage(KEY_IDIOM_MIN_NUMBER) || "9"),

      randomLevelForm: {
        levelCount: 10,
        wordCount: 3,
        blankCount: 3,
        working: false,
        buttonText: "开始",
        storeLevelCount: 0,
        currentPage: 0,
        currentLevel: 0,
      },
    };
  },

  computed: {
    usedWords() {
      return this.grid.map((t) => t.word);
    },

    // 过滤已使用的成语后的列表
    idioms() {
      if (this.selectIdiom === "idiom.clear") {
        return ClearIdioms.filter(
          (t) => t.word === this.value || !this.usedWords.includes(t.word)
        ).map((t) => {
          return {
            ...t,
            count: this.gridStorage[t.word] || 0,
          };
        });
      } else {
        return ClearFillIdioms.filter(
          (t) => t.word === this.value || !this.usedWords.includes(t.word)
        ).map((t) => {
          return {
            ...t,
            count: this.gridStorage[t.word] || 0,
          };
        });
      }
    },

    // 当前选中网格方向
    isSelectVertical() {
      if (this.current_selections.length < 2) {
        return false;
      }

      return (
        this.current_selections[0].colIndex ===
        this.current_selections[1].colIndex
      );
    },

    // 当前选择的网格并排序
    getCurrentSelections() {
      return _.orderBy(
        [...this.current_selections],
        ["rowIndex", "colIndex"],
        ["asc", "asc"]
      );
    },

    getCurrentNode() {
      if (this.getCurrentSelections.length !== 4) {
        return false;
      }

      const start = this.getCurrentSelections[0];
      return this.grid.find(
        (t) =>
          t.rowIndex === start.rowIndex &&
          t.colIndex === start.colIndex &&
          t.vertical === this.isSelectVertical
      );
    },

    isExists() {
      return !!this.getCurrentNode;
    },

    getOtherNode() {
      if (this.getCurrentSelections.length !== 4) {
        return [];
      }

      if (!this.getCurrentNode) {
        return this.grid;
      }

      return this.grid.filter((t) => t.word !== this.getCurrentNode.word);
    },

    getGridAxis() {
      return _.flattenDeep(this.grid.map((t) => t.axis)).map((t) => {
        const [rowIndex, colIndex] = ("" + t)
          .split("_")
          .map((t) => parseInt(t));
        return { rowIndex, colIndex };
      });
    },

    // 参考词列表（用于连接）
    getReference() {
      if (this.getCurrentSelections.length !== 4) {
        return [];
      }

      return this.getCurrentSelections.map((t) => {
        return this.getOtherNode.reduce((word, node) => {
          const index = node.axis.indexOf(`${t.rowIndex}_${t.colIndex}`);
          if (index !== -1) {
            return node.word.split("")[index];
          } else {
            return word;
          }
        }, "");
      });
    },

    // 可选成语列表
    optionalWords() {
      if (this.getCurrentSelections.length !== 4) {
        return [];
      }

      if (this.isSelect) {
        return [];
      }

      const hasRef = this.getReference.some((word) => !!word);
      if (!hasRef) {
        return [];
      }

      return this.getRandomWords();
    },

    gridStorage: {
      get() {
        return this.rawGridStorage;
      },
      set(grid = []) {
        // const storage = jsonParse(getLocalStorage('KEY_IDIOM_USED_COUNT'), {});

        grid.forEach((item) => {
          const key = item.word;
          // console.log(storage, storage[key]);
          if (!this.rawGridStorage[key]) {
            this.rawGridStorage[key] = 0;
          }
          this.rawGridStorage[key]++;
        });

        setLocalStorage(
          KEY_IDIOM_USED_COUNT,
          jsonStringify(this.rawGridStorage, {})
        );
      },
    },

    levelStorage: {
      get() {
        return this.levelNumber;
      },
      set() {
        this.levelNumber++;
        setLocalStorage(KEY_IDIOM_LEVEL_NUMBER, this.levelNumber.toString());
      },
    },

    minIdiom: {
      get() {
        return this.rawMinIdiom;
      },
      set(val) {
        this.rawMinIdiom = val;
        setLocalStorage(KEY_IDIOM_MIN_NUMBER, this.rawMinIdiom.toString());
      },
    },

    blanksCount() {
      const blanks = this.grid.map((t) => {
        return this.getBlanks(t.axis);
      });
      return _.uniq(blanks.flat()).length;
    },
  },

  watch: {
    getCurrentNode() {
      if (this.getCurrentNode) {
        this.value = this.getCurrentNode.word;
      } else {
        this.value = "";
      }
    },

    "randomLevelForm.wordCount"(val) {
      if (this.randomLevelForm.blankCount < val) {
        this.randomLevelForm.blankCount = val;
      }
    },
  },

  methods: {
    __mousedown(cellData, cell, e) {
      // 右键扣字
      if (e.button === 2) {
        cell.isBlank = !cell.isBlank;
      } else {
        this.current_selections = [];
        this.isSelect = true;
        this.__mousemove(cellData);
      }
    },

    __mouseup() {
      this.isSelect = false;

      this.setWord();
    },

    __mousemove: _.debounce(function (e) {
      if (!this.isSelect) {
        return;
      }

      if (
        _.find(
          this.current_selections,
          (t) => t.rowIndex === e.rowIndex && t.colIndex === e.colIndex
        )
      ) {
        return;
      }

      // first
      if (this.current_selections.length === 0) {
        this.current_selections.push(e);
        return;
      }

      // second
      const last = this.current_selections[this.current_selections.length - 1];
      if (this.current_selections.length === 1) {
        if (
          (last.colIndex === e.colIndex || last.rowIndex === e.rowIndex) &&
          (Math.abs(last.rowIndex - e.rowIndex) === 1 ||
            Math.abs(last.colIndex - e.colIndex) === 1)
        ) {
          this.current_selections.push(e);
        }

        return;
      }

      // other
      const diff = this.isSelectVertical
        ? Math.abs(last.rowIndex - e.rowIndex)
        : Math.abs(last.colIndex - e.colIndex);
      if (diff !== 1) {
        return;
      }

      if (this.isSelectVertical && last.colIndex === e.colIndex) {
        this.current_selections.length < 4
          ? this.current_selections.push(e)
          : "";
      } else if (!this.isSelectVertical && last.rowIndex === e.rowIndex) {
        this.current_selections.length < 4
          ? this.current_selections.push(e)
          : "";
      }
    }, 4),

    /**
     * 添加成语节点
     * @param word
     * @private
     */
    __addNode(word) {
      if (!this.isExists && word) {
        this.grid.push({
          word,
          rowIndex: this.getCurrentSelections[0].rowIndex,
          colIndex: this.getCurrentSelections[0].colIndex,
          vertical: this.isSelectVertical,
          axis: this.getCurrentSelections.map(
            (t) => `${t.rowIndex}_${t.colIndex}`
          ),
        });
      }
    },

    /**
     * 删除成语节点
     * @private
     */
    __removeNode() {
      if (this.getCurrentSelections.length !== 4) {
        return;
      }

      const { rowIndex, colIndex } = this.getCurrentSelections[0];
      const index = this.grid.findIndex(
        (t) => t.rowIndex === rowIndex && t.colIndex === colIndex
      );
      if (index !== -1) {
        this.grid.splice(index, 1);
        this.current_selections = [];
      }
    },

    __updateNode(word) {
      if (!this.isExists || !word) {
        return;
      }
      for (let i = 0; i < this.grid.length; i++) {
        const node = this.grid[i];
        if (
          node.vertical === this.isSelectVertical &&
          node.rowIndex === this.getCurrentNode.rowIndex &&
          node.colIndex === this.getCurrentNode.colIndex
        ) {
          this.$set(node, "word", word);
        }
      }
    },

    /**
     * 按坐标获取所属节点
     * @param rowIndex
     * @param colIndex
     * @returns {*}
     * @private
     */
    __getNodeByAxis(rowIndex, colIndex) {
      return this.grid.find((t) => t.axis.includes(`${rowIndex}_${colIndex}`));
    },

    /**
     * 获取坐标节点文字
     * @param rowIndex
     * @param colIndex
     * @returns {string}
     */
    getWordByAxis(rowIndex, colIndex) {
      const node = this.__getNodeByAxis(rowIndex, colIndex);
      if (node) {
        return node.word.split("")[
          node.axis.indexOf(`${rowIndex}_${colIndex}`)
        ];
      }
      return "";
    },

    /**
     * 获取一组有效成语
     * @returns []
     */
    getRandomWords() {
      if (this.getCurrentSelections.length !== 4) {
        return [];
      }

      const canConnIdioms = this.idioms.filter((idiom) => {
        const letters = idiom.word.split("");
        return this.getReference.every((value, index) => {
          if (!value) {
            return true;
          }
          return value === letters[index];
        });
      });

      return Object.freeze(
        _.orderBy(
          canConnIdioms,
          ["count", "rate", "reference"],
          ["asc", "desc", "desc"]
        ).slice(0, 1000)
      );
    },

    /**
     * 在选择的位置填充成语
     * @package isClick
     */
    setWord(isClick = false) {
      // console.log('#setWords', this.getReference);

      if (this.isExists && !isClick) {
        return false;
      }
      const words = this.getRandomWords();
      // console.log(JSON.parse(JSON.stringify(words)));
      let word = words.length && words.random().word;
      if (!word) {
        return false;
      }

      if (this.isExists) {
        this.__updateNode(word);
      } else {
        this.__addNode(word);
      }
      return true;
    },

    onWordChange(word) {
      if (this.isExists) {
        this.__updateNode(word);
      } else {
        this.__addNode(word);
      }
    },

    getBlanks(axis) {
      const grid_items = this.$refs.grid_items;

      let blanks = [];
      axis.forEach((t) => {
        let [y, x] = t.split("_");
        let idx = (y - 1) * 10 + (x - 1);
        if (grid_items[idx].isBlank) {
          blanks.push(t);
        }
      });
      return blanks;
    },

    /**
     * 下载文件
     */
    download: _.debounce(function () {
      const grid_items = this.$refs.grid_items;

      const data = this.grid.map((t) => {
        t.blanks = this.getBlanks(t.axis);
        return _.omit(t, ["axis---"]);
      });
      this.gridStorage = data;

      const data2 = [];
      for (let y = 1; y <= 8; y++) {
        for (let x = 1; x <= 10; x++) {
          let isBlank = grid_items[x - 1 + (y - 1) * 10].isBlank;
          let char = this.getWordByAxis(y, x);

          let item = {
            type: isBlank ? "blank" : char ? "char" : "empty",
          };
          if (isBlank) {
            item._char = char;
          } else {
            item.char = char;
          }
          data2.push(item);
        }
      }

      const blob = new Blob([JSON.stringify(data, null, 2)], {
        type: "application/json,charset=utf-8;",
      });
      FileSaver.saveAs(blob, `level.${this.levelStorage}.json`);
      this.levelStorage++;
    }, 300),

    /**
     * 清空网格
     */
    clear() {
      this.current_selections = [];
      this.grid = [];
      this.$refs.grid_items.forEach((grid) => {
        grid.isBlank = false;
      });
    },

    clearStorage() {
      this.$confirm("确认清除缓存?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true,
      }).then(() => {
        setLocalStorage(KEY_IDIOM_USED_COUNT, JSON.stringify({}));
        setLocalStorage(KEY_IDIOM_LEVEL_NUMBER, 1);
        this.$message({
          type: "success",
          message: "清除成功!",
        });
      });
    },

    /****** 随机生成 ******/
    genSelections(startX, startY, vertical) {
      startX = Math.max(1, startX);
      startY = Math.max(1, startY);
      if (vertical) {
        startY = Math.min(5, startY);
      } else {
        startX = Math.min(7, startX);
      }

      const selections = [];
      const start = vertical ? startY : startX;
      for (let i = start; i < start + 4; i++) {
        selections.push({
          colIndex: vertical ? startX : i,
          rowIndex: vertical ? i : startY,
        });
      }

      this.current_selections = selections;
    },
    randomFirstWord() {
      const rndX = _.random(3, 8);
      const rndY = _.random(2, 5);
      const rndV = Math.random() > 0.5;
      // console.log("randomFirstWord", rndX, rndY, rndV);
      this.genSelections(rndX, rndY, rndV);
      this.setWord();
      _selectionsArr.push(this.current_selections);
    },
    randomNextWord() {
      let retryCount = 50,
        findWord = false;

      do {
        const prevSelections =
          _selectionsArr[_.random(0, _selectionsArr.length - 1)];
        const prevVertical =
          prevSelections[0].colIndex === prevSelections[1].colIndex;

        let rndX, rndY;
        if (prevVertical) {
          rndX = _.random(
            prevSelections[0].colIndex - 3,
            prevSelections[0].colIndex
          );
          // rndY = _.random(
          //   prevSelections[0].rowIndex,
          //   prevSelections[3].rowIndex
          // );
          rndY = prevSelections[Math.random() > 0.5 ? 0 : 3].rowIndex;
        } else {
          // rndX = _.random(
          //   prevSelections[0].colIndex,
          //   prevSelections[3].colIndex
          // );
          rndX = prevSelections[Math.random() > 0.5 ? 0 : 3].colIndex;
          rndY = _.random(
            prevSelections[0].rowIndex - 3,
            prevSelections[0].rowIndex
          );
        }

        const rndV = !prevVertical;
        // console.log("randomNextWord", rndX, rndY, rndV);
        this.genSelections(rndX, rndY, rndV);

        // 并排，重新生成
        if (!this.checkSideBySide()) {
          findWord = this.setWord();
          if (findWord) {
            _selectionsArr.push(this.current_selections);
          }
        }

        // console.log("findWord", findWord, retryCount);
      } while (retryCount-- > 0 && !findWord);
    },
    checkSideBySide() {
      const cur = this.current_selections;
      return _selectionsArr.some((t) => {
        let colOff = 0,
          rowOff = 0;
        t.forEach((grid, i) => {
          colOff += Math.abs(grid.colIndex - cur[i].colIndex);
          rowOff += Math.abs(grid.rowIndex - cur[i].rowIndex);
        });
        let hit = colOff + rowOff <= 8;
        if ((colOff == 0 && rowOff == 16) || (colOff == 16 && rowOff == 0)) {
          hit = true;
        }
        // if (hit) {
        //   console.log(JSON.stringify(this.current_selections), colOff, rowOff);
        // }
        return hit;
      });
    },
    randomBlanks() {
      const axisArr = [];
      const blanks = this.grid.map((t) => {
        const axis = _.shuffle(t.axis);
        axisArr.push(axis.slice(1));
        return axis[0];
      });

      const pad = this.randomLevelForm.blankCount - blanks.length;
      if (pad > 0) {
        blanks.push(..._.shuffle(axisArr.flat()).slice(0, pad));
      }

      const grid_items = this.$refs.grid_items;

      blanks.forEach((t) => {
        let [y, x] = t.split("_");
        let idx = (y - 1) * 10 + (x - 1);
        // console.log("set isBlank", t, idx, this.grid);
        grid_items[idx] && (grid_items[idx].isBlank = true);
      });
    },
    async randomGenLevel(wordCount = 5) {
      this.clear();
      // 不算首词
      wordCount -= 1;

      return new Promise((resolve) => {
        _selectionsArr = [];
        this.randomFirstWord();

        const nextWord = () => {
          setTimeout(() => {
            this.randomNextWord();

            if (--wordCount > 0) {
              nextWord();
            } else {
              this.randomBlanks();

              let data = this.getLevelData();
              data = this.makeCenterData(data);
              resolve(data);
            }
          }, 0);
        };
        nextWord();
      });
    },
    getLevelData() {
      const data = this.grid.map((t) => {
        t = _.clone(t);
        t.blanks = this.getBlanks(t.axis);
        return t;
      });

      return data;
    },
    loadLevelData(data) {
      this.clear();
      this.grid = data;

      const grid_items = this.$refs.grid_items;
      data.forEach((w) => {
        w.blanks.forEach((t) => {
          let [y, x] = t.split("_");
          let idx = (y - 1) * 10 + (x - 1);
          grid_items[idx] && (grid_items[idx].isBlank = true);
        });
      });
    },
    makeCenterData(data) {
      const axis = _.map(data, (t) => t.axis).flat();
      const yArr = _.map(axis, (t) => Number(t.split("_")[0]));
      const xArr = _.map(axis, (t) => Number(t.split("_")[1]));

      const yMax = _.max(yArr),
        yMin = _.min(yArr);
      const xMax = _.max(xArr),
        xMin = _.min(xArr);

      const xOffset = Math.round((10 - xMax - (xMin - 1)) / 2);
      const yOffset = Math.round((8 - yMax - (yMin - 1)) / 2);

      // console.log([xMin, xMax], [yMin, yMax], [xOffset, yOffset]);

      const axisOffset = (axisArr, xOffset, yOffset) => {
        return axisArr.map((item) => {
          let yx = item.split("_");
          let y = Number(yx[0]) + yOffset;
          let x = Number(yx[1]) + xOffset;
          return y + "_" + x;
        });
      };

      const centerData = data.map((t) => {
        t.axis = axisOffset(t.axis, xOffset, yOffset);
        t.blanks = axisOffset(t.blanks, xOffset, yOffset);
        t.colIndex += xOffset;
        t.rowIndex += yOffset;

        return t;
      });

      return centerData;
    },

    async handleStartRandomLevels() {
      const limit = pLimit(1);
      const pWorks = [];

      const start = Date.now();
      this.randomLevelForm.working = true;

      for (let i = 0; i < this.randomLevelForm.levelCount; i++) {
        pWorks.push(
          limit(async () => {
            const data = await this.randomGenLevel(
              this.randomLevelForm.wordCount
            );
            let storeLevelCount = await localforage.length();
            await localforage.setItem(`level-${++storeLevelCount}`, data);

            this.randomLevelForm.buttonText =
              (100 * (i + 1)) / this.randomLevelForm.levelCount + "%";
          })
        );
      }

      await Promise.all(pWorks);
      this.randomLevelForm.working = false;
      this.randomLevelForm.buttonText = "开始";

      this.updatePager();

      console.log("耗时：", (Date.now() - start) / 1000);
    },

    async updatePager() {
      this.randomLevelForm.storeLevelCount = await localforage.length();
    },

    async handleCurrentChange(curPage) {
      // console.log(this.randomLevelForm.currentPage)
      this.randomLevelForm.currentLevel = curPage;
      const data = await localforage.getItem(`level-${curPage}`);
      this.loadLevelData(data);
    },

    async handleReRandomLevel() {
      const data = await this.randomGenLevel(this.grid.length);
      await localforage.setItem(
        `level-${this.randomLevelForm.currentLevel}`,
        data
      );
      this.loadLevelData(data);
    },
    async handleSaveLevel() {
      await localforage.setItem(
        `level-${this.randomLevelForm.currentLevel}`,
        this.getLevelData()
      );
    },
    async handleDownloadAll() {
      let data = {};
      const keys = await localforage.keys();
      const promiseArr = [];
      keys.forEach((key) => {
        promiseArr.push(localforage.getItem(key));
      });
      const values = await Promise.all(promiseArr);

      keys.forEach((key, i) => {
        data[key] = values[i];
      });
      // console.log(data);

      const blob = new Blob([JSON.stringify(data, null, 2)], {
        type: "application/json,charset=utf-8;",
      });
      FileSaver.saveAs(blob, `levels.json`);
    },
  },

  async mounted() {
    this.updatePager();
  },
};
</script>

